import React, { useState } from "react";
import axios from "axios"
import BACKEND from "./Constants/Backend";
import DOMPurify from "dompurify";

const imageAdd = "data:image/jpeg;base64,"
function AddTags() {

    const [postId, setpostId] = useState()
    const [addedTag, setaddedTag] = useState("")
    const [post, setpost] = useState({})
    const [caption, setcaption] = useState("")
    const [body, setbody] = useState("<h3>body will appear here</h3>")
    const [title, settitle] = useState("")
    const [tagsValue, settagsValue] = useState([])
    const [image, setimage] = useState("")
    // console.log(caption, title)
    const createMarkup = (html) => {
        return {
            __html: DOMPurify.sanitize(html),
        };
    };

    const getPost = async () => {
        try {
            const { data } = await axios.get(`${BACKEND}/post/open/${postId}`)
            if (data.success) {
                console.log(data.post)
                setpost(data.post)
                settagsValue(data.post.tags)
                setcaption(data.post.caption)
                settitle(data.post.title)
                setbody(data.post.body)
                setimage(data.post.image)
            }
        } catch (e) { console.log(e) }
    }
    console.log(body)
    const removeItem = (tag) => {
        // console.log("here")
        const removedArray = tagsValue.filter(currTag => currTag != tag)
        settagsValue(removedArray)
    }
    const addThisTag = () => {
        settagsValue([...tagsValue, addedTag])
        setaddedTag("")
    }

    const editThisPost = async () => {
        try {
            console.log("editthis post")
            const { data } = await axios.patch(`${BACKEND}/post/edit/${postId}`,
                { title, caption, tags: tagsValue, body: body, image: image })
            if (data.success) {
                alert("Post updated")
            }
        } catch (e) {
            console.log(e)
        }
    }

    return (
        <div>
            <div style={{ backgroundColor: "grey", border: "1px solid black", display: "flex", flexDirection: "column", margin: "auto", justifyContent: "center", alignItems: "center" }}>
                <div style={{ color: "white", fontWeight: "bold", fontSize: "1.3rem" }}>Enter post id</div>
                <div style={{ display: "flex", marginLeft: "auto", flexDirection: "row", justifycontent: "center", alignitems: "center", width: "57%" }}>
                    <input style={{ width: "12rem" }} value={postId} onChange={value => setpostId(value.target.value)} type="text" placeholder="Enter id" />
                    <button onClick={getPost}>submit</button>
                    <button style={{ marginLeft: "auto", backgroundColor: "white", fontSize: "1.2rem" }} onClick={editThisPost}>Edit This Post</button>

                </div>
            </div>
            <div style={{ border: "2px solid red", justifyContent: "center", display: "flex", flexDirection: "column", margin: "auto", justifyContent: "center", alignItems: "center" }}>
                <div style={{ display: "flex", flexDirection: "row", justifycontent: "center", alignitems: "center", gap: "1rem" }}>
                    {(() => {
                        return tagsValue?.map((tag, index) => {
                            return (<>
                                <div style={{ backgroundColor: "#ebebe0" }}>{tag} <button style={{ marginLeft: "3px" }} onClick={() => removeItem(tag)}>X</button></div>
                            </>)

                        })
                    })()}


                </div>
                <input style={{ width: "12rem" }} value={addedTag} onChange={value => setaddedTag(value.target.value)}></input>
                <button onClick={addThisTag}>Add tag</button>
            </div>
            <div style={{ border: "2px solid black ", display: "flex", flexDirection: "column", margin: "auto", justifyContent: "center", justifycontent: "center", alignItems: "center", margin: "auto" }}>
                <div style={{ display: "flex", flexDirection: "row", gap: "12px", width: "80%" }}>
                    <div ><img src={post.image}
                        style={{ objectFit: "contain", maxWidth: "300px", maxHeight: "300px" }}
                    ></img>

                        <textarea value={image}
                            onChange={value => setimage(value.target.value)}
                            style={{
                                // width: "1000px",
                                // resize: "vertical",
                                // overflow: "hidden",
                                fontSize: "15px",
                                height: "3rem",
                                width: "15rem",
                                padding: "2px"
                            }}
                        ></textarea>
                    </div>

                    <div style={{ display: "flex", flexDirection: "column" }}>
                        <div><span style={{ fontWeight: "600", fontSize: "1.2rem" }}>Title</span>
                            <textarea value={title}
                                onChange={value => settitle(value.target.value)}
                                style={{
                                    width: "1000px",
                                    // resize: "vertical",
                                    overflow: "hidden",
                                    fontSize: "18px",
                                    height: "3.1rem",
                                    padding: "2px"
                                }}
                            ></textarea>
                        </div>

                        <div><div style={{ fontWeight: "600", fontSize: "1.2rem" }}>Caption</div>
                            <textarea
                                value={caption}
                                onChange={value => setcaption(value.target.value)}
                                style={{
                                    minWidth: "800px",
                                    overflow: "hidden",
                                    height: "100%",
                                }}
                            ></textarea>
                        </div>

                        <div style={{ marginTop: "2rem", fontWeight: "bold" }}>
                            BODY
                            <textarea value={body}
                                onChange={value => setbody(value.target.value)}
                                style={{
                                    minWidth: "1000px",
                                    border: "2px solid black ",
                                    height: "700px",
                                }}
                            ></textarea>
                        </div>


                    </div>


                </div>

            </div>
        </div>
    )
}

export default AddTags